footer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 75px;
  background-color: #141414;
  color: #fff;
}

.footer__element {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 20%;
  flex: 1 1 20%;
  padding: 10px 5px;
  min-width: 175px;
}

.footer__element-list {
  text-align: right;
}

.footerLink {
  color: #efefef;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

.footerLink:hover {
  color: #10c9c3;
}

@media (max-width: 768px) {
  footer {
    padding: 50px 35px;
  }
  .footer__element {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 50%;
    flex: 1 1 50%;
    margin-bottom: 15px;
    border-bottom: 2px solid #fff;
  }

  .footer__element-list {
    text-align: left;
  }
}
