.projectCard {
  height: 300px;
  -webkit-box-flex: 1;
  -ms-flex: 1 1 25%;
  flex: 1 1 25%;
  position: relative;
  -webkit-transition: 0.3s cubic-bezier(0.26, 0.56, 0.72, 1.2);
  -o-transition: 0.3s cubic-bezier(0.26, 0.56, 0.72, 1.2);
  transition: 0.3s cubic-bezier(0.26, 0.56, 0.72, 1.2);
  cursor: pointer;
  overflow: hidden;
  border: 2px solid #fff;
}

.projectCard > .statsDiv {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: space-evenly;
  -ms-flex-pack: space-evenly;
  justify-content: space-evenly;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  opacity: 0;
}

.projectCard:hover > .statsDiv {
  background-color: #8888888c;
  opacity: 1;
}

.projectCard:hover {
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
  z-index: 999;
}

.projectCard__photo-holder {
  -o-object-fit: cover;
  object-fit: cover;
  background-size: cover;
  width: 100%;
  height: 100%;
}

.projectCard__statHolder {
  -webkit-box-flex: 1;
  -ms-flex: 1 1 45%;
  flex: 1 1 45%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  padding: 5px;
}

@media (max-width: 920px) {
  .projectCard {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 32%;
    flex: 1 1 32%;
  }
}

@media (max-width: 768px) {
  .projectCard {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 48%;
    flex: 1 1 48%;
  }

  .projectCard__photo-holder {
    background-position: center;
  }
}

@media (max-width: 420px) {
  .projectCard {
    -webkit-box-flex: 1;
    -ms-flex: 1 1 99%;
    flex: 1 1 99%;
  }
}
